<template>
    <div>
      <div class="header">
        搜索
      </div>
      <div class="random">
        <span>随机种子是:</span>{{ seed[randomInt]}}
      </div>
      <div class="inp">
          <form action="/">
          <van-search
          v-model="value"
          show-action
          placeholder="请输入搜索关键词"
          @cancel="onCancel"
          />
          <div class="techer" v-for="item in filteredList" :key=item.id v-show="value">
            <img :src="item.url" alt="ads" class="img" >
            <span>车牌号：</span>
          </div>

          </form>
        </div>
    </div>
</template>

<script>
export default {
  name: 'SearchIndex',
  data () {
    return {
      randomInt: Math.floor(Math.random() * (3)),
      value: '',
      list: [{
        id: 1, name: '濑户环奈', url: require('@/images/707a47402396f9ff550b6679a80c983a418393224.b004838e.jpg')
      }, {
        id: 2, name: '枫可怜', url: require('@/images/OIP-C (2).jpg')
      }, {
        id: 3, name: '三上悠亚', url: require('@/images/R-C.jpg')
      }],
      seed: ['sone-225', 'sone-227', 'sone-336']

    }
  },
  computed: {
    filteredList () {
      if (!this.value) {
        return this.list // 无关键字时显示全部内容
      }
      return this.list.filter(item => {
        // 忽略大小写，匹配名称中包含关键字的项
        return item.name.toLowerCase().includes(this.value.toLowerCase())
      },
      console.log(this.list)
      )
    }

  },
  methods: {
    onCancel () {
      alert('取消了')
    }

  }
}

</script>

<style>
  .header {
    margin: 20px auto;
    text-align: center;
    font-size: 20px;
    }
    .img {
        width:100%;
        height: 100%;
        border-radius: 20px;
    }
    .random {
      text-align: center;
      font-size: 25px;
    }
    .random span {
      color: pink;

    }
    .techer span {
      text-align: center;
      color: 25px;
    }
</style>
