<template>
    <div>
        <div><van-nav-bar
          title="会员登录"
          left-text="返回"
          left-arrow
          @click-left="onClickLeft"
          @click-right="onClickRight"
      />
    </div>
    <div class="myheader">
        <span class="myspan">手机号登录</span>
    </div>
    <div class="mycontent">
        <input type="text" class="myinput" placeholder="请输入手机号码" v-model="mobile">
        <br>
        <input type="text" class="myinput" placeholder="请输入图形验证码">
        <br>
        <input type="text" class="myinput" placeholder="请输入短信验证码">
        <button class="mybutton" @click="getCode">{{ second === totalSecond ? `获取验证码` : `${second}后重新获取` }}</button>
        <div class="button">
            登录
        </div>

    </div>
    </div>
</template>

<script>
import { Toast } from 'vant'

export default {
  name: 'MyDetail',
  data () {
    return {
      totalSecond: 5,
      second: 5,
      timer: null,
      mobile: ''
    }
  },

  methods: {
    onClickLeft () {
      this.$router.back()
    },
    vaildFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        Toast('请输入正确的手机号')
        return false
      }
      return true
    },
    getCode () {
      if (!this.vaildFn()) {
        return
      }
      if (!this.timer && this.totalSecond === this.second) {
        this.timer = setInterval(() => {
          this.second--

          if (this.second <= 0) {
            clearInterval(this.timer)
            this.second = this.totalSecond
            this.timer = null
          }
        }, 1000)
      }
    }
  }

}

</script>

<style>
    .myspan {
        font-size: 30px;
    }
    .myheader {
        margin-top: 80px;
        margin-left: 30px;
    }
    .myinput {
        height: 30px;
        margin-top: 30px;
        border: 0;
        margin-left: 30px;
    }
    .mybutton {
        height: 30px;
        width: 100px;
        border: 0;
        color: orange;
        background-color: white;
    }
    .bottom button {
        border: 1px solid black;
    }
    .button {
        width: 300px;
        height: 40px;
        border: 1px solid;
        border-radius: 10px;
        margin-top: 50px;
        margin-left: 30px;
        background-color: orange;
        color: white;
        text-align: center;
        line-height: 40px;
    }
</style>
