import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/views/layout/index.vue'
import ProDetail from '../views/prodetail/index.vue'
import Search from '@/views/layout/search.vue'
import My from '@/views/layout/my.vue'
import MyDetail from '@/views/prodetail/mydetail.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Layout,
    redirect: '/search',
    children: [
      {
        path: '/search',
        name: 'search',
        component: Search
      },
      {
        path: '/my',
        name: 'my',
        component: My
      }
    ]
  },
  {
    path: '/prodetail/:id',
    name: 'detail',
    component: ProDetail,
    children: [
      {
        path: '/prodetail/mydetail',
        name: 'mydetail',
        component: MyDetail
      }
    ]
  }

]

const router = new VueRouter({
  routes
})

export default router
